import React from "react";
import logo from "./media/logo.svg";
import dots from "./media/dots.svg";
import linkedinLogo from "./media/linkedin-logo.svg";
import angelLogo from "./media/angelicon.svg";
import "./App.scss";
import AnimateHeight from "react-animate-height";
import { Fade } from "react-reveal";
import MDReactComponent from "markdown-react-js";
import { Link, animateScroll as scroll } from "react-scroll";
import Expand from "react-expand-animated";
import HamburgerMenu from "react-hamburger-menu";

const contentful = require("contentful");

class App extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      width: window.innerWidth,
      height: window.innerHeight,
      sizeLimit: 768,
      burgerMenu: false,
      aboutText: "",
      teamData: [],
      portfolioData: [],
      bioOpen: -1,
    };
  }

  componentDidMount() {
    var client = contentful.createClient({
      // This is the space ID. A space is like a project folder in Contentful terms
      space: "kpndngk418ru",
      // This is the access token for this space. Normally you get both ID and the token in the Contentful web app
      accessToken: "6gfR7ml1YUc663Qzrg5JZ3CaWZnj7gEqN46t9VoNcQU",
    });
    client.getEntries({ content_type: "aboutPage" }).then((response) => {
      this.setState({
        aboutText: response.items[0].fields.text.content[0].content[0].value,
      });
    });

    client.getEntries({ content_type: "teamMember" }).then((response) => {
      var tempTeam = response.items;
      var newTeam = [];

      for (var i = 0; i < response.items.length; i++) {
        if (response.items[i].fields.fullName === "Miles Bird") {
          tempTeam.unshift(response.items[i]);
          tempTeam.splice(i + 1, i + 2);
        }
      }

      this.setState({ teamData: tempTeam });
    });

    client.getEntries({ content_type: "portfolioCompany" }).then((response) => {
      var tempPortfolio = [];
      var newPortfolio = [];

      for (var i = 0; i < response.items.length; i++) {
        tempPortfolio.push(response.items[i].fields.companyName);
      }

      tempPortfolio = tempPortfolio.sort();

      for (var i = 0; i < tempPortfolio.length; i++) {
        var j = 0;
        while (tempPortfolio[i] != response.items[j].fields.companyName) {
          j++;
        }
        newPortfolio.push(response.items[j]);
      }

      this.setState({ portfolioData: newPortfolio });
    });
  }

  componentWillUnmount() {}

  updateWindowDimensions() {
    console.log(window.innerWidth);
  }

  handleIterate = (Tag, props, children, level) => {
    if (level === 1) {
      props = {
        ...props,
        className: "zero-margin mt-3 mt-md-0 mt-lg-0 text-left playfair",
      };
    }

    if (Tag === "a") {
      props = {
        ...props,
        className: "playfair red-a",
        target: "_blank",
      };
    }

    return <Tag {...props}>{children}</Tag>;
  };

  handleBurger = () => {
    this.setState({ burgerMenu: !this.state.burgerMenu });
    console.log(this.state.burgerMenu);
  };

  toggleBio = (key) => {
    this.setState({ bioOpen: this.state.bioOpen === key ? -1 : key });
  };

  render() {
    return (
      <div className="full-body">
        <div className="navbar w-100">
          <div className="container">
            <div className="d-hide w-100">
              <Expand open={this.state.burgerMenu}>
                <div className="w-100 burger-buttons transition-all">
                  <div className="row playfair">
                    <div className="col-md mt-2 text-center w-100">
                      <Link
                        activeClass="active"
                        to="about"
                        spy={true}
                        smooth={true}
                        offset={-50}
                        duration={500}
                      >
                        About
                      </Link>
                    </div>
                    <div className="col-md text-center my-2 w-100">
                      <Link
                        activeClass="active"
                        to="team"
                        spy={true}
                        smooth={true}
                        offset={-50}
                        duration={500}
                      >
                        Team
                      </Link>
                    </div>
                    <div className="col mb-3 text-center w-100">
                      <Link
                        activeClass="active"
                        to="portfolio"
                        spy={true}
                        smooth={true}
                        offset={-50}
                        duration={500}
                      >
                        Companies
                      </Link>
                    </div>
                  </div>
                </div>
              </Expand>
              <div className="burger-box p-2 ml-2 mt-2">
                <HamburgerMenu
                  isOpen={this.state.burgerMenu}
                  menuClicked={this.handleBurger}
                  width={25}
                  height={25}
                  strokeWidth={2}
                  rotate={0}
                  color="#b80f0a"
                  animationDuration={0.5}
                />
              </div>
            </div>
            <div className="row m-hide">
              <div className="col nav-logo">
                <Link
                  activeClass="active"
                  to="home"
                  spy={true}
                  smooth={true}
                  offset={-50}
                  duration={500}
                >
                  <img className="nav-logo transition-all" src={logo} />
                </Link>
              </div>
              <div className="col-md-auto d-flex w-40-d">
                <div className="row playfair">
                  <div className="col">
                    <Link
                      activeClass="active"
                      to="about"
                      spy={true}
                      smooth={true}
                      offset={-50}
                      duration={500}
                    >
                      About
                    </Link>
                  </div>
                  <div className="col">
                    <Link
                      activeClass="active"
                      to="team"
                      spy={true}
                      smooth={true}
                      offset={-50}
                      duration={500}
                    >
                      Team
                    </Link>
                  </div>
                  <div className="col">
                    <Link
                      activeClass="active"
                      to="portfolio"
                      spy={true}
                      smooth={true}
                      offset={-50}
                      duration={500}
                    >
                      Companies
                    </Link>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <section className="home d-flex" id="home">
          <div className="container">
            <div className="row">
              <div className="col-md-auto home-text">
                <div className="head-text text-center w-100">
                  <h1 className="tbird-font">T-BIRD</h1>
                  <h2 className="tbird-font">CAPITAL</h2>
                  <h2 className="playfair mt-5">
                    An <a className="red">early-stage</a> venture capital firm
                    and
                    <a className="red"> partner</a> to exceptional
                    entrepreneurs.
                  </h2>
                  <img
                    className="w-100  d-hide"
                    src={require("./media/birdhalf3.png")}
                  />
                </div>
              </div>
              <div className="col-md home-img">
                <img
                  className="m-hide"
                  src={require("./media/birdhalf3.png")}
                />
              </div>
            </div>
          </div>
        </section>
        <section className="about my-5 py-5" id="about">
          <div className="container">
            <div className="row">
              <div className="col-md pl-md-3">
                <h1 className="playfair weight-600 red mb-3">About Us</h1>
                <p className="playfair">{this.state.aboutText}</p>
              </div>
            </div>
          </div>
        </section>
        <section className="team my-5 py-5" id="team">
          <div className="container">
            <h1 className="playfair weight-600 red mb-3">Team</h1>
            {this.state.teamData.map((person, key) => (
              <div className="row mb-5">
                <div className="profile-image col-md d-flex align-items-center">
                  <div className="text-center">
                    <div className="profile-pic w-100">
                      <img
                        className="w-100 h-100"
                        src={person.fields.profilePic.fields.file.url}
                      />
                    </div>
                  </div>
                </div>
                <div className="profile-desc w-75 d-flex align-items-center col-md-auto">
                  <div className="w-100">
                    <div className="w-100 name-sec d-flex">
                      <h3 className="playfair red-a font-weight-600">
                        {person.fields.fullName}
                        <a
                          className="ml-4"
                          href={person.fields.linkedinUrl}
                          target="_blank"
                        >
                          <img
                            className="linkedin-logo m-hide"
                            src={linkedinLogo}
                          />
                        </a>
                      </h3>
                    </div>
                    <div className="m-hide">
                      <MDReactComponent
                        text={person.fields.bio}
                        onIterate={this.handleIterate}
                      />
                    </div>
                    <div className="d-hide bio-expand w-100">
                      <a
                        className="ml-4"
                        href={person.fields.linkedinUrl}
                        target="_blank"
                      >
                        <img
                          className="linkedin-logo transition-all ml-3"
                          src={linkedinLogo}
                        />
                      </a>
                      <button
                        className="playfair m-hide"
                        onClick={() => this.toggleBio(key)}
                      >
                        Read Bio
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </section>
        <section className="portfolio" id="portfolio">
          <div className="container">
            <h1 className="playfair weight-600 red mb-3">
              Investments and Advisory Positions
            </h1>
            <div className="row">
              {this.state.portfolioData.map((company) => (
                <div className="col-auto">
                  <a
                    target="_blank"
                    href={company.fields.websiteUrl}
                    className="company-link red-a"
                  >
                    <div className="img-container mb-3 w-100">
                      <img
                        className="w-100 transition-all"
                        src={company.fields.companyLogo.fields.file.url}
                      />
                    </div>

                    {company.fields.companyName}
                  </a>
                </div>
              ))}
            </div>
          </div>
        </section>
        <section className="footer my-5 playfair text-center">
          <p>
            <a
              className="transition-all"
              href="https://www.linkedin.com/company/tbirdcap/"
              target="_blank"
            >
              Follow us
              <img
                className="linkedin-logo transition-all ml-2"
                src={linkedinLogo}
              />
            </a>
          </p>
          <p>
            <a
              className="transition-all"
              href="https://angel.co/t-bird-capital/syndicate"
              target="_blank"
            >
              Invest with us
              <img
                className="linkedin-logo transition-all ml-1"
                src={angelLogo}
              />
            </a>
          </p>
          <p>Â© T-Bird Capital 2020</p>
        </section>
      </div>
    );
  }
}

export default App;
